import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {TokenService} from "../services/token.service";
import {FilterConfig} from "../search/models/filter/filter-config";

@Component({
  selector: 'wf1-full-width-panel',
  templateUrl: `./full-width-panel.component.html`,
  styles: []
})
export class FullWidthPanelComponent implements OnInit {
  @Input()
  titleText: string;
  @Input()
  linkIcon?: string;
  @Input()
  recordCount = 0;
  @Input()
  filters?: FilterConfig[];
  @Output()
  linkClick = new EventEmitter<string>();

  isInitializing = true;
  isShowingFilterOptions = false;

  constructor(private token: TokenService) {
    this.isInitializing = token.getOauthToken() === null;
  }

  ngOnInit() {
    this.token.authTokenEmitter.subscribe(() => {
      this.isInitializing = false;
    });
  }

  getMainContentClasses(): string {
    return (this.isShowingFilterOptions) ? 'side-panel-with-toolbar-and-filter-and-filter-content' : 'side-panel-with-toolbar-and-filter-content';
  }

  setFilterSize(isShowingFilterOptions: boolean) {
    this.isShowingFilterOptions = isShowingFilterOptions;
  }

  titleLinkClicked() {
    this.linkClick.emit(this.titleText);
  }
}
