import {Component, Input, OnInit} from "@angular/core";
// External
import {select, Store} from "@ngrx/store";
// Models
import {DefaultSort} from "../../models/sort/default-sort";
import {SortDirection} from "../../models/sort/sort-direction";
import {SortOption} from "../../models/sort/sort-option";
// Redux
import {SearchState} from "../../store/state";
import * as SearchActions from "../../store/actions";
import {selectCurrentSearchParam} from "../../../utils";

@Component({
  selector: 'wf1-sort-modal',
  templateUrl: './sort.component.html',
  styleUrls: []
})
export class SortComponent implements OnInit {

  @Input() defaultSort: DefaultSort;
  @Input() options: SortOption[];
  @Input() componentId: string;

  isVisible = false;

  activeSortParam: string;
  activeSortDir: SortDirection;

  constructor(
    private store: Store<SearchState>
  ) {}

  ngOnInit() {
    this.setVisiblility();
    this.setDefaultSort();
  }

  setVisiblility() {
    this.isVisible = true;
    this.store.pipe(select(selectCurrentSearchParam(this.componentId, 'sortModalVisible'))).subscribe((isVisible: boolean) => this.isVisible = isVisible);
  }

  setDefaultSort() {
    if (this.defaultSort) {
      this.store.dispatch(new SearchActions.UpdateSortAction(this.defaultSort.param, this.defaultSort.direction, this.componentId));
    }

    this.store.pipe(select(selectCurrentSearchParam(this.componentId, 'sortParam'))).subscribe((param: string) => this.activeSortParam = param);
    this.store.pipe(select(selectCurrentSearchParam(this.componentId, 'sortDirection'))).subscribe((dir: SortDirection) => this.activeSortDir = dir);
  }

  updateSort(option: SortOption) {
    const direction: SortDirection = option.param === this.activeSortParam ? this.toggleSortDirection(this.activeSortDir) : 'DESC';
    this.store.dispatch(new SearchActions.UpdateSortAction(option.param, direction, this.componentId));
  }

  close() {
    this.store.dispatch(new SearchActions.ToggleSortModalAction(this.componentId));
  }

  toggleSortDirection = (dir: SortDirection) => dir === 'ASC' ? 'DESC' : 'ASC';

  isActiveParam = (param: string) => param === this.activeSortParam;
  isActiveDir = (dir: SortDirection) => dir === this.activeSortDir;
  showArrow = (param: string, dir: SortDirection) => this.isActiveParam(param) && this.isActiveDir(dir);

}
