import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { TokenService } from "../services/token.service";
import { NavigationService } from "../services/navigation.service";

@Component({
    selector: 'wf1-basic-sidebar-panel',
    templateUrl: `./basic-sidebar-panel.component.html`,
    styles: []
})
export class BasicSidebarPanelComponent implements OnInit {
    public TOOLTIP_DELAY = 500;

    @Input()
    titleText: string;
    @Input()
    linkIcon?: string;
    @Input()
    linkAlignLeft?: boolean;
    @Input()
    linkTitle?: string;

    @Input() showBackButton: boolean = false;

    @Output()
    linkClick = new EventEmitter<string>();

    isInitializing = true;

    constructor(
        public token: TokenService,
        public navigationService: NavigationService
    ) {
        this.isInitializing = token.getOauthToken() === null;
    }

    ngOnInit() {
        this.token.authTokenEmitter.subscribe(() => {
            this.isInitializing = false;
        });
        if (!this.linkTitle) {
            if (this.linkIcon == "launch") {
                this.linkTitle = "Launch Dual Window";
            } else if (this.linkIcon == "arrow_back") {
                this.linkTitle = "Back";
            } else {
                this.linkTitle = null;
            }

        }
    }

    getTitleIconClasses(): string {
        return this.linkAlignLeft ? 'side-panel-link-button side-panel-link-button-left' : 'side-panel-link-button side-panel-link-button-right';
    }

    getMainContentClasses(): string {
        return 'side-panel-content';
    }

    titleLinkClicked() {
        this.linkClick.emit(this.titleText);
    }

    titleBackClicked() {
        this.navigationService.back()
    }
}
