import {Component, EventEmitter, Input, Output} from "@angular/core";
import {TokenService} from "../services/token.service";
import {BasicSidebarPanelComponent} from "../basic-sidebar-panel/basic-sidebar-panel.component";

@Component({
    selector: 'wf1-location-sidebar-panel',
    templateUrl: `./location-sidebar-panel.component.html`,
    styles: []
})
export class LocationSidebarPanelComponent extends BasicSidebarPanelComponent {
  @Input()
  set location(location: [number, number]) {
    if (location) {
      this.selectedLocation = location;
    }
  }

  selectedLocation: [number, number];

  @Output()
  locationSelect = new EventEmitter<[number, number]>();

  isInitializing = true;

//   constructor(public token: TokenService) {
//       super(token);
//   }

  locationSelected(coordinates: [number, number]) {
      this.locationSelect.emit(coordinates);
  }
}
