import {Component, Input, OnInit, TemplateRef} from "@angular/core";
import {TokenService} from "../services/token.service";

@Component({
  selector: 'wf1-full-width-container',
  templateUrl: './full-width-container.component.html',
  styles: []
})
export class FullWidthContainerComponent implements OnInit {
  @Input()
  titleText: string;
  @Input()
  mainContentTemplate: TemplateRef<any>;

  public isInitializing = true;

  constructor(private token: TokenService) {
    this.isInitializing = token.getOauthToken() === null;
  }

  ngOnInit() {
    this.token.authTokenEmitter.subscribe(() => {
      this.isInitializing = false;
    });
  }

}
