import {Action} from "@ngrx/store";

export const MAPTOOL_WINDOW_NAME = 'maptool-application-window';
export const WFROF_WINDOW_NAME = 'wfrof-application-window';
export const WFNROF_WINDOW_NAME = 'wfnrof-application-window';
export const WFIM_WINDOW_NAME = 'wfim-application-window';


export const WFIM_LIST_COMPONENT_ID = 'searchIMMap';
export const WFROF_LIST_COMPONENT_ID = 'searchROFMap';
export const WFNROF_LIST_COMPONENT_ID = 'searchNROFMap';
export const WFIM_TABLE_COMPONENT_ID = 'searchIM';
export const WFROF_TABLE_COMPONENT_ID = 'searchROF';
export const WFNROF_TABLE_COMPONENT_ID = 'searchNROF';

export enum MessageType {
  ACTION = 'action', // Application actions type
  READY = 'ready', // Indicate to parent the child is ready to receive messages
  WINDOW_CLOSING = 'window-closing', // Indicate to the parent the child window is closing
  ACKNOWLEDGE = 'acknowledge', // Acknowledge receipt of a message back to the sender
}

export class Message {
  type: MessageType;
  action?: Action;
}

export class MessagePacket {
  id: string;
  originWindowName: string;
  date: Date;
  message: Message;
}

export class WindowConfig {
  fullscreen: boolean;
  rememberWindowSize: boolean;
}

export class ApplicationWindow {
  id: string;
  isParent: boolean;
  url?: string;
  windowName?: string;
  windowConfig?: WindowConfig;
  window: Window;
  messageQueue: MessagePacket[];
}
