import {Component, Input} from "@angular/core";
import {TokenService} from "../services/token.service";
import {FilterConfig} from "../search/models/filter/filter-config";
import {BasicSidebarPanelComponent} from "../basic-sidebar-panel/basic-sidebar-panel.component";
import {DefaultSort} from "../search/models/sort/default-sort";
import {SortOption} from "../search/models/sort/sort-option";

@Component({
  selector: 'wf1-filter-sidebar-panel',
  templateUrl: `./filter-sidebar-panel.component.html`,
  styles: []
})
export class FilterSidebarPanelComponent extends BasicSidebarPanelComponent {
  @Input()
  recordCount = 0;
  @Input()
  filters?: FilterConfig[];
  @Input()
  defaultSort?: DefaultSort;
  @Input()
  sortOptions?: SortOption[];
  @Input()
  componentId: string;

  isInitializing = true;
  isShowingFilterOptions = false;

//   constructor(public token: TokenService) {
//     super(token);
//   }

  getMainContentClasses(): string {
    return (this.isShowingFilterOptions) ? 'side-panel-with-toolbar-and-filter-and-filter-content' : 'side-panel-with-toolbar-and-filter-content';
  }

  setFilterSize(isShowingFilterOptions: boolean) {
    this.isShowingFilterOptions = isShowingFilterOptions;
  }
}
