import {Component, OnInit} from "@angular/core";
import {MapService} from "../services/map.service";

export interface BaseMap {
  key: string;
  title: string;
}

@Component({
  selector: 'wf1-wfml-basemap-selector',
  templateUrl: './wfml-basemap-selector.component.html',
  styleUrls: ['./wfml-basemap-selector.component.scss']
})
export class WFMLBasemapSelectorComponent implements OnInit {
  basemaps: BaseMap[] = [];

  constructor(private mapService: MapService) {
  }

  ngOnInit() {
    if (this.mapService.isMapInitialized('map')) {
      this.initializeBasemaps();
    } else {
      this.mapService.mapInstanceEmitter.asObservable().subscribe(data => {
        this.initializeBasemaps();
      });
    }
  }

  initializeBasemaps() {
    this.basemaps = this.mapService.getBasemaps('map');
  }

  displayBasemap(index: number) {
    this.mapService.setBasemap('map', index);
  }
}
